import React, { Component } from "react";
import "./about.css";
class About extends Component {
  render() {
    return (
      <div className="about">
        <h3>About me:</h3>
        <h6>
          Senior undergraduate student at IIIT Naya Raipur. Currently working on
          the Tcs as a SDE intern for a final year 6-month internship. Always
          interested to work on app and web development, love to work on rest
          API. I wanted to explore the -GraphQl, Go, Express, Firebase, and an
          open-source project. I have done many projects on react and flutter
          also worked in ml/dl field all the source code are available on my
          GitHub. Currently working on the web app that uses redux, react,
          express, MongoDB along with some DevOps tool like Jenkins, docker on a
          GCP Linux server.{" "}
        </h6>
      </div>
    );
  }
}

export default About;



